import React from 'react';

const Group = React.lazy(() => import('./views/Group'));
const Light = React.lazy(() => import('./views/Light'));


const routes = [
  { path: '/', exact: true, name: 'Lights', component: Group },
  { path: '/groups', exact: true, name: 'Groups', component: Group },
  { path: '/lights', name: 'Lights', component: Light }
];

export default routes;
